import MockAdapter from 'axios-mock-adapter';
import SignUpJSON from './signUpForm.json';
import ProfileJSON from './profileForm.json';

export function mockProfileService(mock: MockAdapter) {
  mock.onGet(/\/form\/sign-up/).reply(() => {
    return [200, SignUpJSON];
  });

  mock.onPost(/\/form\/sign-up/).reply(() => {
    return [200, true];
  });

  mock.onGet(/\/form\/profile/).reply(() => {
    return [200, ProfileJSON];
  });

  mock.onPost(/\/form\/profile/).reply(() => {
    return [200, true];
  });

  return mock;
}
