export type IFieldTypes =
  | 'input'
  | 'small_radio'
  | 'big_radio'
  | 'cheap_radio'
  | 'autocomplete'
  | 'range_slider'
  | 'photo';
export type IFieldIcon = 'user' | 'location';
export type IFieldSource = 'location';
export type IFieldRadioVariant = {
  value: string | number;
  name: string;
};

export interface IFieldForm {
  field_id: number;
  field_type: IFieldTypes;
  field_title?: string;
  field_default_value?: string | IFieldRadioVariant;
}

export interface IFieldInputForm extends IFieldForm {
  field_placeholder?: string;
  field_icon?: IFieldIcon;
}

export interface IFieldRadioForm extends IFieldForm {
  field_title?: string;
  field_variants?: IFieldRadioVariant[];
}

export interface IFieldAutocompleteForm extends IFieldForm {
  field_placeholder?: string;
  field_icon?: IFieldIcon;
  field_source?: IFieldSource;
}

export interface IFieldRangeSliderForm extends IFieldForm {
  field_min: number;
  field_max: number;
  field_min_placeholder: string;
  field_max_placeholder: string;
}

export type IFieldPhotoForm = IFieldForm;

export type IField =
  | IFieldInputForm
  | IFieldRadioForm
  | IFieldAutocompleteForm
  | IFieldPhotoForm
  | IFieldRangeSliderForm;

export interface IFormPage {
  title: string;
  progress_title: string;
  fields: IField[];
}

export type IForm = IFormPage[];

export type ISignUpFetchResult = IForm;

export function isPhoto(field: IField): field is IFieldPhotoForm {
  return field.field_type === 'photo';
}

export function isInput(field: IField): field is IFieldInputForm {
  return field.field_type === 'input';
}

export function isRangeSlider(field: IField): field is IFieldRangeSliderForm {
  return field.field_type === 'range_slider';
}

export function isRadio(field: IField): field is IFieldRadioForm {
  return (
    field.field_type === 'small_radio' ||
    field.field_type === 'big_radio' ||
    field.field_type === 'cheap_radio'
  );
}

export function isAutocomplete(field: IField): field is IFieldAutocompleteForm {
  return field.field_type === 'autocomplete';
}
