import { FieldBox } from 'components/blocks/Form/Form.styles';
import { TextField } from 'components/ui';
import React from 'react';
import { isInput } from 'services/profile';
import { IFieldProps } from 'components/blocks/Form/Fields/Fields.types';

export function FormInput({ field, handleChange, handleBlur, values }: IFieldProps) {
  if (!isInput(field)) {
    return null;
  }

  return (
    <FieldBox>
      <TextField
        onChange={handleChange}
        onBlur={handleBlur}
        value={values[field.field_id]}
        id={String(field.field_id)}
        label={field.field_placeholder}
        variant={'standard'}
      />
    </FieldBox>
  );
}
