import React from 'react';
import { Routes, Route, Link, Outlet, useNavigate } from 'react-router-dom';
import { Login } from 'components/pages/Login';
import { SignUp } from 'components/pages/SignUp';
import { Profile } from 'components/pages/Profile';
import styled from '@emotion/styled';
import { Button } from 'components/ui/MaterialUI';
import { GenericIcon } from 'components/ui/GenericIcon';

const HeaderTitle = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 64px;

  ::after {
    position: absolute;
    content: '';
    height: 1px;
    width: 100vw;
    background: ${({ theme }) => theme.colors.background.secondary};
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
  }

  ${Button} {
    min-width: auto;

    :first-of-type {
      padding: 0;
    }
  }
`;

function FormLayout() {
  const navigate = useNavigate();
  return (
    <div>
      <HeaderTitle>
        <Button onClick={() => navigate(-1)}>
          <GenericIcon viewBox="0 0 10 16" iconName="arrowLeft" />
        </Button>
        <Button onClick={() => navigate('/')}>Main</Button>
      </HeaderTitle>
      <Outlet />
    </div>
  );
}

export default function Router() {
  return (
    <>
      <Routes>
        <Route path="/*" element={<div>404</div>} />
        <Route
          path="/"
          element={
            <div>
              <Link to={'form/sign-up/1'}>Go to SignUp</Link>
              <br />
              <Link to={'form/profile/1'}>Go to Profile</Link>
            </div>
          }
        />
        <Route path="/form" element={<FormLayout />}>
          <Route path="sign-up/:formPage" index element={<SignUp />} />
          <Route path="profile/:formPage" index element={<Profile />} />
        </Route>
        <Route path="/authenticate" element={<Login />} />
      </Routes>
    </>
  );
}
