import React from 'react';

import { IGenericIconProps } from './GenericIcon.types';
import { RootStyled as Root } from './GenericIcon.styles';
import { GenericIconMap } from 'components/ui/GenericIcon/GenericIconMap';

export function GenericIcon({
  rootStyled: RootStyled = Root,
  iconName,
  ...props
}: IGenericIconProps) {
  return <RootStyled {...props}>{GenericIconMap[iconName]}</RootStyled>;
}
