import React from 'react';

import { ILayoutProps } from './Layout.types';
import { Content, RootStyled as Root } from './Layout.styles';
import { useScreenOrientation } from 'components/common/hooks';

export function Layout({ rootStyled: RootStyled = Root, children, ...props }: ILayoutProps) {
  const { trueMobileVH } = useScreenOrientation();
  return (
    <RootStyled vh={trueMobileVH} {...props}>
      <Content vh={trueMobileVH}>{children}</Content>
    </RootStyled>
  );
}
