import { useState, useEffect } from 'react';

function getOrientation() {
  const orientation = window.orientation;

  switch (true) {
    case orientation === 90 || orientation === -90:
      return 'landscape';

    default:
      return 'portrait';
  }
}

export function useScreenOrientation() {
  const [orientation, setOrientation] = useState(getOrientation());
  const [trueMobileVH, setTrueMobileVH] = useState(() => window.innerHeight * 0.01);

  const updateOrientation = () => {
    setOrientation(getOrientation());
  };

  const updateMobileVh = () => {
    setTimeout(() => {
      setTrueMobileVH(() => window.innerHeight * 0.01);
    }, 0);
  };

  useEffect(() => {
    window.addEventListener('orientationchange', updateOrientation);
    window.addEventListener('orientationchange', updateMobileVh);
    window.addEventListener('resize', updateMobileVh);
    return () => {
      window.removeEventListener('orientationchange', updateOrientation);
      window.removeEventListener('orientationchange', updateMobileVh);
      window.removeEventListener('resize', updateMobileVh);
    };
  }, []);

  return { orientation, trueMobileVH } as const;
}
