import { FieldBox } from 'components/blocks/Form/Form.styles';
import { TextField, Autocomplete } from 'components/ui';
import React from 'react';
import { isAutocomplete } from 'services/profile';
import { IFieldProps } from 'components/blocks/Form/Fields/Fields.types';

const exampleOptions = [
  { id: 1, title: 'Option 1' },
  { id: 2, title: 'Option 2' },
  { id: 3, title: 'Option 3' },
  { id: 4, title: 'Option 4' },
  { id: 5, title: 'Option 5' },
  { id: 6, title: 'Option 6' },
  { id: 7, title: 'Option 7' },
];

export function FormAutocomplete({ field, values, setFieldValue }: IFieldProps) {
  if (!isAutocomplete(field)) {
    return null;
  }

  return (
    <FieldBox>
      <Autocomplete
        multiple
        id="tags-standard"
        options={exampleOptions}
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        getOptionLabel={(option) => option?.title}
        defaultValue={[exampleOptions[1]]}
        onChange={(_, value) => {
          setFieldValue(String(field.field_id), value);
        }}
        value={values[field.field_id]}
        renderInput={(params) => (
          <TextField {...params} variant="standard" label={field.field_placeholder} />
        )}
      />
    </FieldBox>
  );
}
