import { StoreEnhancer, AnyAction, Reducer } from '@reduxjs/toolkit';
import { enableBatching } from 'redux-batched-actions';

/**
 * Enhance store with `redux-batched-actions`.
 * @param createStore
 * @returns Enhanced store creator.
 */
export const batchedActionsEnhancer: StoreEnhancer = (createStore) => (reducer, preloadedState) =>
  createStore(
    enableBatching(reducer as Reducer<unknown, AnyAction>) as typeof reducer,
    preloadedState
  );
