import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { Formik, FormikProps, FormikValues } from 'formik';

import { IFormProps } from './Form.types';
import { RootStyled as Root, Fields, Titles, ProgressLabel, Title } from './Form.styles';
import { IForm, IField, IFieldTypes } from 'services/profile';
import { Button, CircularProgress } from 'components/ui';
import { useScreenOrientation } from 'components/common/hooks';
import {
  FormInput,
  FormBigRadio,
  FormSmallRadio,
  FormAutocomplete,
  FormCheapRadio,
  FormRangeSlider,
  FormPhoto,
  IFieldProps,
} from 'components/blocks/Form/Fields';
import { useLocation, useNavigate, useParams } from 'react-router-dom';

const FieldsMap: Record<IFieldTypes, (props: IFieldProps) => JSX.Element> = {
  input: FormInput,
  small_radio: FormSmallRadio,
  big_radio: FormBigRadio,
  cheap_radio: FormCheapRadio,
  autocomplete: FormAutocomplete,
  photo: FormPhoto,
  range_slider: FormRangeSlider,
};

function getField(field: IField, formikProps: FormikProps<FormikValues>) {
  const Field = FieldsMap[field.field_type];

  return <Field key={field.field_id} field={field} {...formikProps} />;
}

export function Form({ rootStyled: RootStyled = Root, onFetch, onSubmit, ...props }: IFormProps) {
  const { formPage } = useParams();
  const currentFormPage = Number(formPage);
  const [fetching, setFetching] = useState(false);
  const [formPages, setFormPages] = useState<IForm>([]);
  const navigate = useNavigate();
  const location = useLocation();
  const initialValues = useMemo(() => {
    if (formPages.length) {
      return formPages.reduce((acc, current) => {
        current.fields.forEach(
          ({ field_id, field_default_value }) => (acc[field_id] = field_default_value)
        );
        return acc;
      }, {});
    }

    return {};
  }, [formPages]);
  const { trueMobileVH } = useScreenOrientation();
  const handleNext = useCallback(() => {
    const pathname = location.pathname
      .split('/')
      .map((path, index, array) => {
        if (array.length - 1 === index) {
          return currentFormPage < formPages.length ? currentFormPage + 1 : currentFormPage;
        }

        return path;
      })
      .join('/');

    navigate({ pathname });
  }, [currentFormPage, formPages.length]);

  useEffect(() => {
    if (onFetch) {
      setFetching(true);
      onFetch().then((r) => {
        setFormPages(r);
        setFetching(false);
      });
    }
  }, [onFetch, setFetching, setFormPages]);

  if (formPages.length === 0) {
    return null;
  }

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={(values, { setSubmitting }) => {
        onSubmit().then(() => {
          setSubmitting(false);
        });
      }}
    >
      {(formikProps) => (
        <RootStyled vh={trueMobileVH} onSubmit={formikProps.handleSubmit} {...props}>
          <Titles>
            <h4>
              <CircularProgress
                size={30}
                variant="determinate"
                value={(currentFormPage / formPages.length) * 100}
              />
              <ProgressLabel>{currentFormPage}</ProgressLabel>
              <Title>{formPages[currentFormPage - 1].progress_title}</Title>
            </h4>
            <h1>{formPages[currentFormPage - 1].title}</h1>
          </Titles>
          <Fields>
            {formPages[currentFormPage - 1].fields.map((field) => getField(field, formikProps))}
          </Fields>
          <Button onClick={handleNext} variant="contained" fullWidth>
            Next
          </Button>
        </RootStyled>
      )}
    </Formik>
  );
}
