import { configureStore } from '@reduxjs/toolkit';
import { useDispatch } from 'react-redux';
import { errorLogger, batchedActionsEnhancer } from 'store/common/utils';

import { IState, AppDispatch } from './types';
import { authenticateReducer } from 'store/authenticate/reducers';

/**
 * Create application Redux-store.
 * @param preloadedState - Preloaded state from server.
 * @returns Redux-store.
 */
export function createStore(preloadedState?: IState) {
  return configureStore({
    reducer: {
      authenticate: authenticateReducer,
    },
    middleware: (getDefaultMiddleware) =>
      getDefaultMiddleware({
        immutableCheck: false,
        serializableCheck: false,
      }).concat(errorLogger),
    enhancers: (defaultEnhancers) => [batchedActionsEnhancer, ...defaultEnhancers],
    devTools: process.env.NODE_ENV !== 'production',
    preloadedState,
  });
}

export const useAppDispatch = () => useDispatch<AppDispatch>();
