import { Action } from '@reduxjs/toolkit';

export function isPendingAction(actionType: string) {
  return function (action: Action<string>): action is Action<string> {
    return action.type.endsWith('/pending') && action.type.replace('/pending', '') === actionType;
  };
}

export function isFulfilledAction(actionType: string) {
  return function (action: Action<string>): action is Action<string> {
    return (
      action.type.endsWith('/fulfilled') && action.type.replace('/fulfilled', '') === actionType
    );
  };
}

export function isRejectedAction(actionType: string) {
  return function (action: Action<string>): action is Action<string> {
    return action.type.endsWith('/rejected') && action.type.replace('/rejected', '') === actionType;
  };
}
