import MockAdapter from 'axios-mock-adapter';
import { client } from 'services/axios';

import { IState, createStore } from '.';
import { mockAuthenticateService } from 'services/authenticate/authenticate.mock/authenticateMockService';
import { mockProfileService } from 'services/profile/profile.mock/profileMockService';

export function createMockStore(
  preloadedState?: IState,
  mockService?: (mock: MockAdapter) => MockAdapter
) {
  const mock = new MockAdapter(client, {
    delayResponse: 500,
  });

  if (mockService) {
    mockService(mock);
  } else {
    mockAuthenticateService(mock);
    mockProfileService(mock);
  }

  return createStore(preloadedState);
}
