import { client } from 'services/axios';
import { IAuthenticateResult } from 'services/authenticate/authenticate.types';

export async function authenticate() {
  let data: IAuthenticateResult = null;

  try {
    ({ data } = await client.get(`/authenticate`).then((result) => result));
  } catch (error) {
    if (typeof error === 'string') {
      throw new Error(error);
    }
  }

  return data;
}
