import { createAction, createAsyncThunk } from '@reduxjs/toolkit';
import * as authenticateService from 'services/authenticate';
import { IThunkApiConfig } from 'store';
import { batchActions } from 'redux-batched-actions';

export const setAuthenticate = createAction<boolean>('authenticate/setAuthenticate');
export const setAccount = createAction<authenticateService.IAccount>('authenticate/setAccount');

export const fetchAuthenticate = createAsyncThunk<void, void, IThunkApiConfig>(
  'authenticate/fetchAuthenticate',
  async (_, { dispatch }) => {
    const account = await authenticateService.authenticate();

    dispatch(batchActions([setAccount(account), setAuthenticate(true)]));
  }
);
