import React, { useCallback } from 'react';

import { ILayoutProps } from './Login.types';
import {
  ContentLogin,
  FooterLogin,
  HeaderFormLogin,
  HeaderLogin,
  LoginButton,
  RootStyled as Root,
} from './Login.styles';
import { GenericIcon } from 'components/ui/GenericIcon';
import { Logo } from 'components/ui/Logo';
import { useText } from 'components/common/hooks/useText';
import { useScreenOrientation } from 'components/common/hooks';
import { fetchAuthenticate, useAppDispatch } from 'store';

export function Login({ rootStyled: RootStyled = Root, ...props }: ILayoutProps) {
  const dispatch = useAppDispatch();
  const HeaderFormLoginText = useText('Sign up');
  const privacyPolice = useText('Privacy Police');
  const { trueMobileVH } = useScreenOrientation();

  const loginCallback = useCallback(() => {
    dispatch(fetchAuthenticate());
  }, []);

  return (
    <RootStyled vh={trueMobileVH} {...props}>
      <HeaderLogin>
        <Logo />
      </HeaderLogin>
      <ContentLogin>
        <HeaderFormLogin>{HeaderFormLoginText}</HeaderFormLogin>
        <LoginButton onClick={loginCallback}>
          <GenericIcon iconName={'apple'} /> Gmail
        </LoginButton>
        <LoginButton onClick={loginCallback}>
          <GenericIcon iconName={'yandex_color'} /> Yandex
        </LoginButton>
        <LoginButton onClick={loginCallback}>
          <GenericIcon iconName={'apple'} /> Apple
        </LoginButton>
        <LoginButton onClick={loginCallback}>
          <GenericIcon iconName={'facebook_color'} /> Facebook
        </LoginButton>
        <LoginButton onClick={loginCallback}>
          <GenericIcon iconName={'twitter_color'} /> Twitter
        </LoginButton>
      </ContentLogin>
      <FooterLogin>{privacyPolice}</FooterLogin>
    </RootStyled>
  );
}
