import { FieldBox, RangeInputs, FieldTitle } from 'components/blocks/Form/Form.styles';
import { Slider, TextField } from 'components/ui';
import React, { ChangeEvent, FocusEvent } from 'react';
import { isRangeSlider } from 'services/profile';
import { IFieldProps } from 'components/blocks/Form/Fields/Fields.types';

export function FormRangeSlider({
  field,
  setFieldValue,
  handleChange,
  handleBlur,
  values,
}: IFieldProps) {
  if (!isRangeSlider(field)) {
    return null;
  }

  const handleInputValue = (event: ChangeEvent<HTMLInputElement>) => {
    const name = event.currentTarget.name;
    const value = [...values[field.field_id]];
    const currentValue = Number(event.currentTarget.value);

    value[name === 'min' ? 0 : 1] = currentValue;

    setFieldValue(String(field.field_id), value);
  };

  const handleBlurInput = (event: FocusEvent<HTMLInputElement>) => {
    const name = event.currentTarget.name;
    const value = [...values[field.field_id]];
    const currentValue = Number(event.currentTarget.value);
    let validatedValue = currentValue;

    switch (name) {
      case 'min':
        if (validatedValue < field.field_min) {
          validatedValue = field.field_min;
        }

        if (validatedValue > value[1]) {
          validatedValue = value[1];
        }
        break;
      case 'max':
        if (validatedValue > field.field_max) {
          validatedValue = field.field_max;
        }

        if (validatedValue < value[0]) {
          validatedValue = value[0];
        }
        break;
    }

    value[name === 'min' ? 0 : 1] = validatedValue;

    setFieldValue(String(field.field_id), value);
  };

  return (
    <FieldBox>
      {field.field_title && <FieldTitle>{field.field_title}</FieldTitle>}
      <RangeInputs>
        <TextField
          label={field.field_min_placeholder}
          variant="standard"
          type="number"
          onChange={handleInputValue}
          onBlur={handleBlurInput}
          name="min"
          value={values[field.field_id][0]}
        />
        <TextField
          label={field.field_max_placeholder}
          variant="standard"
          type="number"
          onChange={handleInputValue}
          onBlur={handleBlurInput}
          name="max"
          value={values[field.field_id][1]}
        />
      </RangeInputs>
      <Slider
        id={String(field.field_id)}
        name={String(field.field_id)}
        onBlur={handleBlur}
        onChange={handleChange}
        value={values[field.field_id]}
        disableSwap
        min={field.field_min}
        max={field.field_max}
      />
    </FieldBox>
  );
}
