import Axios from 'axios';
import { SERVICE_BASE_URL, IS_DEV_SERVER, API_PREFIX } from 'env';

const { CancelToken } = Axios;

/**
 * Default Axios client.
 */
export const client = Axios.create({
  baseURL: API_PREFIX ? `${SERVICE_BASE_URL}/${API_PREFIX}` : SERVICE_BASE_URL,
  timeout: 60000,
  params: {},
});

const tokenCancellations = new Map<unknown, () => void>();

/**
 * Cancel token by key.
 * @param key - Associated key with token.
 */
export function cancelTokenByKey(key: unknown) {
  if (tokenCancellations && tokenCancellations.has(key)) {
    const funcKey = tokenCancellations.get(key);

    if (typeof funcKey === 'function') {
      funcKey();
    }
  }
}

/**
 * Cancel previous token by key and create new one.
 * @param key - Associated key with token.
 * @param signal - External abort signal to cancel token.
 * @returns Cancel token.
 */
export function getSingleCancelToken(key: unknown, signal?: AbortSignal) {
  cancelTokenByKey(key);

  const source = CancelToken.source();

  if (signal) {
    const sourceCancellation = () => {
      signal.removeEventListener('abort', sourceCancellation);
      source.cancel();
      tokenCancellations.delete(key);
    };

    signal.addEventListener('abort', sourceCancellation);
    tokenCancellations.set(key, sourceCancellation);
  } else {
    const sourceCancellation = () => {
      source.cancel();
      tokenCancellations.delete(key);
    };

    tokenCancellations.set(key, sourceCancellation);
  }

  return source;
}

if (IS_DEV_SERVER) {
  client.defaults.headers.common.ssl_client_s_dn =
    'CN=test_user@test.lk,OU=Mobile Users,O=VTB,C=RU';
}
