import { isRejected, Middleware, isRejectedWithValue } from '@reduxjs/toolkit';

/**
 * Log a warning and show a toast!
 */
export const errorLogger: Middleware = () => (next) => (action) => {
  if (isRejected(action) && action?.error?.message !== 'Cancel') {
    if (isRejectedWithValue(action)) {
      console.error({ message: action?.error?.message, type: 'error' });

      return;
    }

    if (action?.error?.message.includes('500')) {
      console.error({
        message: 'В данный момент сервис недоступен. Попробуйте позже!',
        type: 'error',
      });
    } else {
      console.error({ message: action?.error?.message, type: 'error' });
    }
  }

  return next(action);
};
