import { createReducer } from '@reduxjs/toolkit';

import { isPendingAction, isFulfilledAction, isRejectedAction } from 'store/common/utils';
import { IAuthenticateState } from './types';
import { setAccount, setAuthenticate } from './actions';

export function getAuthenticateInitialState(): IAuthenticateState {
  return {
    account: null,
    authenticated: false,
    loading: false,
    activeRequestsCount: 0,
  };
}

export const authenticateReducer = createReducer(getAuthenticateInitialState(), (builder) => {
  builder
    /**
     * basic cases:
     */
    .addCase(setAccount, (state, action) => {
      state.account = action.payload;
    })
    .addCase(setAuthenticate, (state, action) => {
      state.authenticated = action.payload;
    })
    /**
     * Rest cases:
     */
    .addMatcher(isPendingAction('authenticate/fetchAuthenticate'), (state) => {
      state.activeRequestsCount++;
      state.loading = true;
    })
    .addMatcher(isFulfilledAction('authenticate/fetchAuthenticate'), (state) => {
      state.loading = --state.activeRequestsCount > 0;
    })
    .addMatcher(isRejectedAction('authenticate/fetchAuthenticate'), (state) => {
      state.loading = --state.activeRequestsCount > 0;
    });
});
