import { client } from 'services/axios';
import { ISignUpFetchResult } from 'services/profile/profile.types';

export async function signUpGET(formName: string) {
  let data: ISignUpFetchResult = null;

  try {
    ({ data } = await client.get(`/form/${formName}`).then((result) => result));
  } catch (error) {
    if (typeof error === 'string') {
      throw new Error(error);
    }
  }

  return data;
}

export async function signUpPOST(formName: string) {
  let data: boolean = null;

  try {
    ({ data } = await client.post(`/form/${formName}`).then((result) => result));
  } catch (error) {
    if (typeof error === 'string') {
      throw new Error(error);
    }
  }

  return data;
}
