import {
  FieldBox,
  PhotoContainer,
  PhotoField,
  PhotoFieldContainer,
  PhotoFieldWrapper,
  PhotoIcon,
} from 'components/blocks/Form/Form.styles';
import React, { ChangeEvent, useEffect, useRef, useState } from 'react';
import { isPhoto } from 'services/profile';
import { IFieldProps } from 'components/blocks/Form/Fields/Fields.types';
import UserEmpty from 'assets/images/user_photo_empty.svg';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';

const initialState = (initial: undefined | string[]) => () => initial || ['', '', '', ''];

export function FormPhoto({ field, setFieldValue, values }: IFieldProps) {
  if (!isPhoto(field)) {
    return null;
  }

  const ref = useRef<HTMLInputElement>(null);
  const [urls, setUrls] = useState<string[]>(initialState(values[field.field_id]));
  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    const name = Number(event.currentTarget.name);
    const fileReader = new FileReader();
    fileReader.readAsArrayBuffer(event.currentTarget.files[0]);
    fileReader.onload = () => {
      const blob = new Blob([fileReader.result]);
      const url = URL.createObjectURL(blob);

      setUrls((prev) => {
        const current = [...prev];

        current[name] = url;

        return current;
      });
    };
  };

  useEffect(() => {
    setFieldValue(String(field.field_id), urls);
  }, [urls]);

  return (
    <FieldBox>
      <PhotoFieldWrapper>
        {urls.map((url, index) => (
          <PhotoField key={index}>
            <PhotoIcon htmlFor={`photo_${index}`}>
              <span>{url === '' ? <AddIcon /> : <EditIcon />}</span>
            </PhotoIcon>
            <PhotoFieldContainer htmlFor={`photo_${index}`}>
              <PhotoContainer isEmpty={url === ''}>
                <img src={url === '' ? UserEmpty : url} />
              </PhotoContainer>
              <input
                name={String(index)}
                onChange={handleChange}
                ref={ref}
                id={`photo_${index}`}
                type="file"
                accept="image/png, image/jpeg, image/jpg"
                hidden
              />
            </PhotoFieldContainer>
          </PhotoField>
        ))}
      </PhotoFieldWrapper>
    </FieldBox>
  );
}
