import React, { useMemo } from 'react';
import { css, Global } from '@emotion/react';
import 'assets/style/sanitize.css';
import { fonts, sizes } from 'components/theme';

const createGlobalCSS = () => css`
  html,
  body {
    margin: 0;
    padding: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    ${fonts.boldStyle};
    margin: 0;
  }

  h1 {
    font-size: ${sizes.fontSizes.xxxl};
    line-height: 44px;
  }

  h2 {
    font-size: ${sizes.fontSizes.xl};
    line-height: 36px;
  }

  h3 {
    font-size: ${sizes.fontSizes.lg};
    line-height: 28px;
  }

  h4 {
    font-size: ${sizes.fontSizes.s};
    line-height: 24px;
  }

  h5 {
    font-size: ${sizes.fontSizes.xs};
    line-height: 20px;
  }

  video {
    width: 100%;
  }

  #ModalContainer {
    position: absolute;
    z-index: 100;
  }

  * {
    box-sizing: border-box;
  }

  button {
    font-family: ${fonts.font}, sans-serif;
  }
`;

export function GlobalStyleApp() {
  const globalCSS = useMemo(() => createGlobalCSS(), []);
  return <Global styles={globalCSS} />;
}
