import { FieldBox, FieldTitle } from 'components/blocks/Form/Form.styles';
import { FormControl, FormControlLabel, Radio, RadioGroup } from 'components/ui';
import React from 'react';
import { isRadio } from 'services/profile';
import { IFieldProps } from 'components/blocks/Form/Fields/Fields.types';

export function FormCheapRadio({ field, handleChange, handleBlur, values }: IFieldProps) {
  if (!isRadio(field)) {
    return null;
  }

  return (
    <FieldBox type={field.field_type}>
      <FormControl>
        {field.field_title && <FieldTitle>{field.field_title}</FieldTitle>}
        <RadioGroup
          row
          id={String(field.field_id)}
          name={String(field.field_id)}
          onBlur={handleBlur}
          onChange={handleChange}
          value={values[field.field_id]}
        >
          {field.field_variants.map(({ name, value }) => (
            <FormControlLabel
              key={value}
              value={value}
              control={<Radio hidden />}
              label={name}
              className={`${
                String(values[field.field_id]) === String(value) ? 'checked' : 'not-checked'
              }`}
            />
          ))}
        </RadioGroup>
      </FormControl>
    </FieldBox>
  );
}
