import React, { useEffect, useRef } from 'react';
import { selectAuthenticated, setAuthenticate, useAppDispatch } from 'store';
import { useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';

export function Authenticate() {
  const dispatch = useAppDispatch();
  const authenticated = useSelector(selectAuthenticated);
  const location = useLocation();
  const navigate = useNavigate();
  const firstPathname = useRef(location.pathname);

  useEffect(() => {
    if (window.localStorage.getItem('login') === 'true') {
      dispatch(setAuthenticate(true));
    } else {
      switch (true) {
        case location.pathname !== '/authenticate' && !authenticated:
          navigate('/authenticate');
          break;
        case location.pathname === '/authenticate' && authenticated:
          window.localStorage.setItem('login', 'true');
          navigate(firstPathname.current === '/authenticate' ? '/' : firstPathname.current);
      }
    }
  }, [authenticated]);

  useEffect(() => {
    if (location.pathname === '/authenticate' && authenticated) {
      navigate('/');
    }
  }, [location.pathname, authenticated]);

  return <>{null}</>;
}
